import React, { Component } from 'react';
import './UserMenu.css';

class UserMenu extends Component {
    constructor() {
        super()
    
        this.state = {
          activeItem: 0,
          items: [{
                name: 'Tigy Line'
                },
                {name: 'Mi Perfil'},
                {name: 'Grupos'},
                {name: 'Opciones'},
                {name: 'Salir'}]
        }
      }
    
      handleItemClick(index) {
        this.setState({
          activeItem: index,
        })
      }
    render() {
        return (
            <div className="user-menu">
                <ul onclick="window.myFunction(event)" id="user-menu">
                    <li className="active"><i className="far fa-list-alt"></i>Tigy Line</li>
                    <li><i className="fas fa-user"></i>Mi Perfil</li>
                    <li><i className="fas fa-user-friends"></i>Grupos</li>
                    <li><i className="fas fa-cogs"></i>Opciones</li>
                    <li><i className="fas fa-sign-out-alt"></i>Salir</li>
                </ul>
            </div>
            /*<div className="user-menu">
                <ul id="user-menu">
                    {this.state.items.map((item, index) =>
                        <li
                        key={index}
                        className={this.state.activeItem === index ? 'navigation--active' : ''}
                        onClick={this.handleItemClick.bind(this, index)}
                        >
                        {item.name}
                        </li>
                    )}
                </ul>
            </div>*/
        );
    }
}

export default UserMenu;